import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import { Carousel, Col } from 'react-bootstrap';

export default function StayComponent({ roomType, price, amenities, onClick }) {

    const images = [{ "name": "Room1", "image": "https://images.unsplash.com/photo-1560448205-4d9b3e6bb6db?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80" }, { "name": "Room2", "image": "https://images.unsplash.com/photo-1522771739844-6a9f6d5f14af?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1051&q=80" }, { "name": "Room3", "image": "https://images.unsplash.com/photo-1513694203232-719a280e022f?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1049&q=80" }]

    return (
        <Col >
            <Card className="text-center" style={{ maxHeight: "70%", border: "none" }}>

                <Card.Body>

                    <Carousel controls={false}>

                        {images.map((item, index) => {
                            return (<Carousel.Item interval={3000} key={index}>
                                <img
                                    className="d-block w-100 custom-rounded"
                                    src={item.image}
                                    alt={item.name}

                                />
                            </Carousel.Item>);
                        })
                        }
                    </Carousel>

                    <p className="card-header-title">
                        {roomType}
                    </p>

                    <h4 className="my-1" >Amenitites</h4>

                    {(amenities.length > 200) ? <p className="text-center smallerFont">{amenities}</p> : <p className="text-center">{amenities}</p>}

                    <div className="btn btn-primary custom-button">Rs. {price}
                    </div>

                    <Button variant="link" className="btn btn-link">
                        <p
                            onClick={onClick}
                            color="black"
                        >TERMS &amp; CONDITIONS</p>
                    </Button>
                </Card.Body>

            </Card>

        </Col >
    );
}